<template>
  <div id='app' class="hello">
   <div class="">  
    <h1>{{ msg }}</h1>
    <h3>{{coin1}} X {{coin2}}</h3>
   </div> 

    <div class="container">      
        <div class="row padding-block">
           <div class="col-lg-4 offset-lg-1">
       
          <autocomplete :search="search"
           placeholder="Pesquise por moedas"
           aria-label="Pesquise por moedas"         
           :get-result-value="getResultValue" 
            @submit="handleSubmit"
            auto-select />

           </div>
           <div class="col-lg-2">
                 <button  @click="getCurrencyValues" class="btn btn-circle btn-xl btn-outline-success">
                        <i class="fa fa-dollar-sign"></i>
                 </button>
             
           </div>
           <div class="col-lg-4">
       
          <autocomplete :search="search"
           placeholder="Pesquise por moedas"
           aria-label="Pesquise por moedas"           
           :get-result-value="getResultValue2" 
            @submit="handleSubmit2"
            auto-select />

           </div>
         </div>


         <div class="row padding-block">
           <div class="col-lg-8 col-lg-offset-2" v-if="resultCoin" >

              <div class="form-inline padding-form" >
                 <input type='number' class="form-control" v-model="c"  min='0' />
                 <button class="btn btn-outline-success" @click="calculate" >Calcular valor em {{resultCoin.comparedTo._symbol}}</button> 
              </div>

              <div class="table-responsive">
               <table class="table table-stripped table-hover ">
                  <thead class="">
                     <tr>
                        <th>Sigla</th>
                        <th>Preço
                        <i class="fa fa-dollar-sign" ></i></th>
                     </tr>    
                  </thead>
                  <tbody>
                       <tr>
                          <td>{{resultCoin.base._symbol}}</td>  
                          <td>{{resultCoin.base._value}}</td>
                       </tr>
                       <tr>    
                          <td>{{resultCoin.comparedTo._symbol}}</td> 
                          <td>{{resultCoin.comparedTo._value}}</td>
                       </tr>   
                       <tr colspan="2">    
                          <td>Data</td><td>{{resultCoin.date | formatDate}}</td> 
                       </tr>
                  </tbody>
               </table> 
              </div>  

           </div>
        </div>    
    </div>



  
     <router-view />


  </div>
</template>

<script>

import Autocomplete from '@trevoreyre/autocomplete-vue'
import { asyncLoading } from 'vuejs-loading-plugin'

export default {
  name: 'CurrencyComponent',
  props: {
    msg: String
  },
  data:()=>({
    test: [],
    resultCoin: null,
    AllCoins: null,
    barTitle: '',
    c: '',
    coin1:'',
    coin2:'',
  }),
  components: {
    Autocomplete
  },
  methods:{
    loadData(config)
    {      
        axios(config)
         .then(res => {
             this.test = res.data.rates
           }).catch(err => console.log(err))
            
    },
     loadCoin(config)
    {      
       
       let result =  axios(config).then(res =>  res.data)
          .catch(err => console.log(err))

          this.$loading(true)
           asyncLoading(result).then((res)=>{
                this.resultCoin =  res.rates
           }).catch((err)=>{ console.log(err) })
            
    },
    loadCompare(config){
       let result =  axios(config)
         .then(res =>  res.data)
            .catch(err => console.log(err))
          this.$loading(true)
           asyncLoading(result).then((res)=>{
                this.resultCoin =  res.data
           }).catch(()=>{})
    },
    search(input) {
      if (input.length < 1) { return [] }
      return Object.keys(this.test).filter((el, k)=> {
        return el.toLowerCase()
          .startsWith(input.toLowerCase())
      })
    },
    getResultValue(result) {
      return result
    },    
    handleSubmit(result) {  
      if(this.coin1 == '' || this.coin1 != result)  
        { this.coin1 = result 
          if(this.coin1 != '' && this.coin2 != '')
          {
             this.getCurrencyValues()
          }   
        }
    },
    getResultValue2(result) {
      return result
    },    
    handleSubmit2(result) {  
 
      if(this.coin2 == '' || this.coin2 != result)  
        { this.coin2 = result 
          if(this.coin1 != '' && this.coin2 != '')
          {
             this.getCurrencyValues()
          }
        }  
    },
    getCurrencyValues(){
      if(this.coin1 != '' && this.coin2 != '')
      {    
        this.loadCompare({url: `/currency/${this.coin1}/${this.coin2}`})
      }
    },
    calculate()
    {
       if(this.coin1 != '' && this.coin2 != '')
      {
       this.loadCompare({url: `/currency/${this.coin1}/${this.coin2}/${this.c}`})
      }
    }
  
  },
  created() {
      this.loadData({url: '/currency'})
  }
}
</script>

